<template>
  <div id="index" ref="appRef">
    <div class="bg">
      <dv-loading v-if="loading">Loading...</dv-loading>
      <div v-else class="host-body">
        <div class="d-flex jc-center">
          <dv-decoration-10 class="dv-dec-10" :color="['#79CDCD','rgba(202,225,255,0.3)']"/>
          <div class="d-flex jc-center">
            <dv-decoration-8 class="dv-dec-8" :color="['#79CDCD','rgba(202,225,255,0.3)']" />
            <div class="title">
              <span class="title-text">个人运动健康数据</span>
              <dv-decoration-6 class="dv-dec-6" :reverse="true" :color="['#50e3c2', '#67a1e5']"/>
            </div>
            <dv-decoration-8 class="dv-dec-8" :reverse="true" :color="['#79CDCD','rgba(202,225,255,0.3)']"/>
          </div>
          <dv-decoration-10 class="dv-dec-10-s" :color="['#79CDCD','rgba(202,225,255,0.3)']"/>
        </div>

        <!-- 第二行 -->
        <div class="d-flex jc-between px-2">
          <div class="d-flex aside-width">
            <div class="react-left ml-4 react-l-s">
              <span class="react-left"></span>
              <span class="text">{{ dateYear }} {{ dateWeek }} {{ dateDay }}</span>
            </div>
            <div class="react-left ml-3">
                <span class="text fw-b">visualiazation-FP</span>
            </div>
          </div>
          <div class="d-flex aside-width">
            <div class="react-right bg-color-subtitle mr-3">

            </div>
            <div class="react-right mr-4 react-l-s">
              <span class="react-after"></span>
              <div class="block">
                <el-date-picker
                  v-model="dateValue.date"
                  align="right"
                  type="date"
                  value-format='yyyy-MM-dd'
                  placeholder="选择日期"
                  :picker-options="pickerOptions">
                </el-date-picker>
              </div> 
            </div>
          </div>
        </div>

        <div class="body-box">
          <!-- 第三行数据 -->
          <div class="content-box">
            <div>
              <dv-border-box-12 :color="['rgba(202,225,255,0.3)','#79CDCD']">
                <centerLeft1 />
              </dv-border-box-12>
            </div>
            <!-- 中间 -->
            <div>
                <center />
            </div>
            <!-- 中间 -->
            <div>
                <centerRight2 />
            </div>
            <div>
              <dv-border-box-12 :color="['rgba(202,225,255,0.3)','#79CDCD']">
                <centerRight1 />
              </dv-border-box-12>
            </div>
          </div>

          <!-- 第四行数据 -->
          <div class="bottom-box">
            <dv-border-box-12 :color="['rgba(202,225,255,0.3)','#79CDCD']">
              <bottomLeft />
            </dv-border-box-12>
            <dv-border-box-12 :color="['rgba(202,225,255,0.3)','#79CDCD']">
              <bottomRight />
            </dv-border-box-12>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import ScreenAdapt from "../utils/ScreenAdapt";
  import { formatTime } from '../utils/index.js'
  import centerLeft1 from './centerLeft1'
  import centerRight1 from './centerRight1'
  import centerRight2 from './centerRight2'
  import center from './center'
  import bottomLeft from './bottomLeft'
  import bottomRight from './bottomRight'

  export default {
    mixins: [ ScreenAdapt ],
    data() {
      return {
        timing: null,
        loading: true,
        dateDay: null,
        dateYear: null,
        dateWeek: null,
        weekday: ['周日', '周一', '周二', '周三', '周四', '周五', '周六'],
        decorationColor: ['#568aea', '#000000'],
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          },
          shortcuts: [{
            text: '今天',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '一周前',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
        },
        dateValue: {
          date:"2022-12-01"
        }
      }
    },
    components: {
      centerLeft1,
      centerRight1,
      centerRight2,
      center,
      bottomLeft,
      bottomRight
    },
    mounted() {
      this.timeFn()
      this.cancelLoading()
    },
    provide() {
      return {
        dateValue: this.dateValue
      }
    },
    beforeDestroy () {
      clearInterval(this.timing)
    },
    methods: {
      timeFn() {
        this.timing = setInterval(() => {
          this.dateDay = formatTime(new Date(), 'HH: mm: ss')
          this.dateYear = formatTime(new Date(), 'yyyy-MM-dd')
          this.dateWeek = this.weekday[new Date().getDay()]
        }, 1000)
      },
      cancelLoading() {
        setTimeout(() => {
          this.loading = false
        }, 100)
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '../assets/scss/index.scss';
</style>
